@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://use.fontawesome.com/releases/v5.0.8/css/all.css);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --main-font: "Roboto", sans-serif;
}

.goggles-search {
  font-family: var(--main-font);
  min-height: 100vh;
  min-width: 100vw;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
}

.goggles-search .goggles-container {
  width: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 20%;
  position: fixed;
}

.goggles-search .goggles-container span {
  font-size: 80px;
  font-weight: 600;
  margin-bottom: 40px;
  cursor: context-menu;
}

.goggles-search .goggles-container span .blue-label {
  color: #4285f4;
}

.goggles-search .goggles-container span .red-label {
  color: #ea4335;
}

.goggles-search .goggles-container span .yellow-label {
  color: #fbbc05;
}

.goggles-search .goggles-container span .green-label {
  color: #34a853;
}

.goggles-search .goggles-container .goggles-search-bar {
  width: 80%;
  border-radius: 25px;
  box-shadow: 0 0 5px rgb(187, 183, 183);
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 20px;
  grid-gap: 30px;
  gap: 30px;
  margin-bottom: 30px;
}

.goggles-search .goggles-search-bar i {
  font-size: 15px;
  color: rgb(100, 98, 98);
}

.goggles-search .goggles-search-bar .typewriter h1 {
  font-weight: 600;
  font-size: 18px;
  overflow: hidden;
  border-right: 0.15em solid #000;
  white-space: nowrap;
  margin: 0;
  margin-right: auto;
  letter-spacing: 0.15em;
  -webkit-animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
          animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end infinite;
}

@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #000;
  }
}

@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #000;
  }
}

.goggles-search .goggles-search-bar img {
  width: 11px;
  margin-left: auto;
}

.goggles-search .websites-list {
  width: 70%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.goggles-search .websites-list .list-tem-container {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  height: 110px;
  border-radius: 5px;
}

.goggles-search .websites-list .list-tem-container:hover {
  background-color: #e8e8e9;
}

.goggles-search .websites-list .circle-container {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #050505;
  background-color: #f1f3f4;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.goggles-search .websites-list .circle-container i {
  font-size: 24px;
}

.goggles-search .websites-list .circle-container img {
  width: 24px;
}

.goggles-search .websites-list .linkeIn {
  color: #0a66c2;
}

.goggles-search .websites-list .github {
  color: #24292e;
}

.goggles-search .websites-list .cloud_download {
  color: #74c0fc;
}

.goggles-search .websites-list a {
  color: inherit;
  text-decoration: inherit;
}

.goggles-result {
  font-family: var(--main-font);
  min-height: 100vh;
  min-width: 100vw;
  background-color: #ffffff;
}

.goggles-result .goggles-container {
  display: flex;
  flex-direction: column;
}

.goggles-result .goggles-container span {
  font-size: 30px;
  font-weight: 600;
  cursor: context-menu;
}

.goggles-result .goggles-container .goggles-result-head {
  width: 100%;
  padding: 25px 20px;
  display: flex;
  flex-direction: row;
  grid-gap: 30px;
  gap: 30px;
}

.goggles-result .goggles-container span .blue-label {
  color: #4285f4;
}

.goggles-result .goggles-container span .red-label {
  color: #ea4335;
}

.goggles-result .goggles-container span .yellow-label {
  color: #fbbc05;
}

.goggles-result .goggles-container span .green-label {
  color: #34a853;
}

.goggles-result .goggles-container .goggles-result-bar {
  width: 700px;
  border-radius: 25px;
  box-shadow: 0 0 5px rgb(187, 183, 183);
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 20px;
  grid-gap: 30px;
  gap: 30px;
  margin-bottom: 30px;
}

.goggles-result .goggles-result-bar i {
  font-size: 18px;
  color: #4285f4;
}

.goggles-result .goggles-result-bar img {
  height: 20px;
}

.goggles-result .goggles-result-bar h1 {
  font-size: 16px;
  white-space: nowrap;
  letter-spacing: 0.15em;
}

.goggles-result .goggles-result-bar .goggles-result-bar-actions {
  display: flex;
  flex-direction: row;
  grid-gap: 20px;
  gap: 20px;
  margin-left: auto;
  justify-content: center;
  align-items: center;
}

.goggles-result .goggles-result-bar .vertical-divider {
  border-left: 2px solid rgb(197, 201, 197);
  height: 25px;
}

